    {
  

        .repeater-wrap {
            width: 100%;
            position: relative;
            //overflow: hidden;// dont allow overflow breaks the sticky

            #grid-wraps{
            width: 100%;
            position: relative;
            display: block;
            // min-height: 50vw;//review - 5 rows atm if item is 10vw
            min-height:  25vw;//review - 5 rows atm if item is 5vw

                #background-map{
                width:100%;
                position: absolute;
                top: 0;
                z-index: -1;
                opacity: 1;
                pointer-events: none;

                    img{
                    display: block;
                    position: relative;    
                    width: 100%;
                    height: auto;   
                    }

                }// #background-map
            }

        }

        /* ---- grid options (top) ---- */

        .grid-options-wrap {
            display: block;// allow editing if not logged in aswell - 
            // TO DO review other css aswell; // display only if logged in + admin on
            position: sticky;
            z-index: 89; //review this; ensure not above navigation; but above everything else
        /*  @include fluid-type(
                top,
                map-get($breakpoints, xs),
                map-get($breakpoints,xxl),
                map-get($lineheight, xs) * 2.5,
                map-get($lineheight,xxl) * 2.5
            );*/
            background-color: nth($scheme, 2);
            border-bottom: 1px solid;
            border-color: nth($scheme, 3);
            background-color: nth($scheme, 3);
            color: nth($scheme, 2);

            .grid-options {
                .btn {
                    // text-align: left;
                    @media (min-width: map-get($breakpoints, xxs)) {
                        text-align: left;
                    }
                } //.btn

                .response-message-wrap {
                    pointer-events: none;
                    position: absolute;
                    opacity: 0;
                    display: block;
                    right: 0;
                    left: 0;
                    text-align: center;
                    // left:0px;

                    .response-message {
                        // @include center-align(absolute, 0);
                        color: red;
                    }

                    &.response-message-active {
                        opacity: 1;
                    }
                } // .response-message-wrap
            } //.grid-options
        } //.grid-options-wrap

        /* ---- grid toggle ---- */

        .grid-toggles {
            background: nth($scheme, 2);
            width: 100%;
            height: auto;
            position: sticky;
            pointer-events: none;
            // display: block;
            // display:none;
            z-index: 29; //review
            text-align: center;
            border-bottom: 1px solid;
            border-color: nth($scheme, 3);
            @include fluid-type(
                height,
                map-get($breakpoints, xs),
                map-get($breakpoints,xxl),
                map-get($lineheight, xs) * 2,
                map-get($lineheight,xxl) * 2
            );
            @include fluid-type(
                line-height,
                map-get($breakpoints, xs),
                map-get($breakpoints, xxl),
                map-get($lineheight, xs) * 2,
                map-get($lineheight,xxl) * 2
            );
            @include fluid-type(
                top,
                map-get($breakpoints, xs),
                map-get($breakpoints,xxl),
                map-get($lineheight, xs) * 2.5,
                map-get($lineheight,xxl) * 2.5
            );

            @media (min-width: map-get($breakpoints, md)) {
                border-bottom: none;
                background: transparent;
                height: 100%;
                position: absolute;
                top: 0;
                bottom: 0;
            }
            @media (min-width: map-get($breakpoints, lg)) {
                display: block;
            }

            .toggle-list {
                .grid-toggle {
                    pointer-events: initial;
                    display: block;
                    width: 100%;
                    cursor: pointer;
                    position: relative;
                    // width: fit-content;
                    margin: auto;
                    // @include fluid-type(top, map-get($breakpoints, xs), map-get($breakpoints,xxl), map-get($lineheight, xs)*2, map-get($lineheight,xxl)*2);

                    &.active-layer {
                        // background-color: red;
                    }
                } //.grid-toggle

                @media (max-width: map-get($breakpoints, md)) {
                margin-top: 0px!important;//ensure no offets applied on mobile
                }

                @media (min-width: map-get($breakpoints, md)) {
                    position: fixed;
                    top: 50%;
                    width: 100%;

                    .grid-toggle {
                        width: initial;

                        @include fluid-type(
                            font-size,
                            map-get($breakpoints, xs),
                            map-get($breakpoints,xxl),
                            map-get($fontsize, xs) * 2,
                            map-get($fontsize,xxl) * 2
                        );
                        @include fluid-type(
                            line-height,
                            map-get($breakpoints, xs),
                            map-get($breakpoints,xxl),
                            map-get($lineheight, xs) * 2,
                            map-get($lineheight,xxl) * 2
                        );

                        &.active-layer {
                            //  -color: red;
                        }
                    } //.grid-toggle
                } //   @media (min-width: map-get($breakpoints, lg))
            } //.toggle-list

            &.toggles-active {
                .toggle-button {
                    display: none;
                }
                .toggle-list {
                    .grid-toggle {
                        pointer-events: initial;
                        //  width: 100%;
                        position: relative;
                        display: none;

                        &.active-layer {
                            // background-color: green;
                            display: inline-block;
                        }
                    } //.grid-toggle
                } //.toggle-list
            } //  &.toggles-active

            &.toggles-active.toggles-on,
            &.toggles-on {
                bottom: 0;
                height: var(--site-height);

                @media (min-width: map-get($breakpoints, lg)) {
                height: auto;
                }

                // top:0px;
                display: flex;
                flex-direction: column;
                position: fixed; //review
                border-bottom:0px;

                .toggle-button {
                    // display: none;
                }
                .toggle-list {
                    height: inherit;

                    display: block;
                    position: relative;
                    overflow: hidden;

                    .grid-toggle {
                        pointer-events: initial;
                        display: block;
                        position: relative;
                        clear: both;
                        width: fit-content;
                        position: relative;

                        @media (max-width: map-get($breakpoints, md)) {
                            border-bottom: 1px solid;
                            border-color: nth($scheme, 3);
                            width: 100%;
                            height: 33.33%;
                            line-height:calc(var(--site-height) / 3);
                            //33.33vh;
                            @include fluid-type(
                                font-size,
                                map-get($breakpoints, xs),
                                map-get($breakpoints,xxl),
                                map-get($fontsize, xs) * 2,
                                map-get($fontsize,xxl) * 2
                            );
                            //  @include fluid-type(line-height, map-get($breakpoints, xs), map-get($breakpoints,xxl), map-get($lineheight, xs)*2, map-get($lineheight,xxl)*2);
                        }

                        &.active-layer {
                            opacity: 0.5;
                        }
                        &:last-of-type{
                        border-bottom:0px;
                        }
                    } //.grid-toggle
                } //.toggle-list
            } //.toggles-on
        } //.grid-toggles

        /* ---- grid layers ---- */

        .grid-layers {
            position: relative;
            // background-color: red;
            width: 100%;

            .grid-layer {
                position: fixed;
                opacity: 0;
                //display: none;
                width: 100%;
                height: inherit;
                display: block;

                form {
                    position: relative;
                    //overflow:hidden; remove so options can be "sticky"
                }

                .layer-title {
                    display: none; // review - do we even need this?

                    @media (min-width: map-get($breakpoints, md)) {
                        display: block;
                    }
                } //.layer-title

                &.active-layer {
                    z-index: 9;
                    opacity: 1;
                    display: block;
                    position: relative;
                } //&.active-layer
            } //.grid-layer
        } //.grid-layers

        /* ---- grid ---- */

        .grid {
            max-width: 100vw; //review
            width: 100%; //review - this will cause issue in the gutenber UI
            // overflow: visible;// so vertical ofsett can be seend
            // overflow: hidden; // otherwise horizontal offset overflows
            // overflow-y: visible; // need to ensur the option selectmenu can overflow - is htis going to be an issue?
            overflow-x:hidden;
            overflow-y: hidden;  //cant have any overflow - yet also means the drop downs are only 1 grid itme heigt
            margin: 0; // maybe use padding instead if needed to add space?
            // height: 50vw;//review - 5 rows atm - need for horizontal layout if item is 10vw
            height: 25vw;//review - 5 rows atm if item is5vw

       
            min-width: 100%;//review this 
        }

        /* clear fix */
        .grid:after {
            content: '';
            display: block;
            clear: both;
        }

        /* ---- .layout-grid-item---- */
        .grid-sizer {
            width: 100%; //$width1;
            height: auto; // $height1;

            @media (min-width: map-get($breakpoints, md)) {
